import { createRouter, createWebHashHistory, RouteRecordRaw } from 'vue-router'

const routes: Array<RouteRecordRaw> = [
	{
		path: '/authorization/:routerPath',
		name: 'Authorization',
		component: () => import('@/pages/example/Authorization.vue')
	},
	{
		path: '/',
		name: 'Home',
		component: () => import('@/pages/example/Index.vue')
	},
	{
		path: '/wxpay',
		name: 'wxpay',
		component: () => import('@/pages/example/Pay.vue')
	},
	{
		path: '/user',
		name: 'User',
		component: () => import('@/pages/example/User.vue')
	},
]
console.log(createRouter)
const router = createRouter({
  history: createWebHashHistory(),
  routes
})

function auth(routerPath: string){
	// wx0d99d9b8109d8ae7
	const appid = 'wx0d99d9b8109d8ae7';
	const redirectUri = encodeURIComponent('http://tm.melinked.com/wx-h5/#/authorization/'+routerPath);
	const scope = 'snsapi_userinfo'; // snsapi_userinfo
	location.href=`https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&response_type=code&scope=${scope}&redirect_uri=${redirectUri}&state=STATE#wechat_redirect`
}

router.beforeEach((to, from, next) => {
	// 路由白名单
	const whitelist = ['Home', 'Authorization'];
	const {name,path} = to;
	let flag = false;
	for(const item of whitelist){
		if(item==name){
			flag = true;
		}
	}
	if (flag) {
		next();
	} else {
		const user = sessionStorage.getItem('user');
		console.log(user);
		if(user !=null && user !=''){
			next();
		}else{
			auth(path.substring(1));
		}
	}
})
export default router
