"use strict";

import axios from "axios";
import router from '@/router';

axios.defaults.headers['Content-Type'] = 'application/json;charset=UTF-8';
axios.defaults.headers['Accept'] = 'application/json';
let requestCount = 0; // 请求次数
const loadingInstance: any = null;
const config = {
  // baseURL: process.env.baseURL || process.env.apiUrl || ""
  baseURL: 'http://172.17.7.17:9080/',
  // timeout: 10 * 1000, // Timeout
  withCredentials: true, // Check cross-site Access-Control
};

const _axios = axios.create(config);

function message(error: string){
  if(error.toString().indexOf('timeout') !== -1){
    return '请求超时';
  }
  console.log(error)
}
_axios.interceptors.request.use(
  function(config) {
    if(requestCount === 0){
      // loadingInstance = Loading.service({ fullscreen: true, background: 'rgba(0, 0, 0, 0.5)'});
    }
    requestCount++;
    config.headers.common['token'] = sessionStorage.getItem('token');
    return config;
  },
  function(error) {
    return Promise.reject(error);
  }
);

// Add a response interceptor
_axios.interceptors.response.use(
  function(response) {
    requestCount--;
    if(requestCount === 0){
      // loadingInstance.close();
    }
    return response.data;
  },
  error => {
    requestCount--;
    if(requestCount === 0){
      // loadingInstance.close();
    }
    router.push({name: '/500', params: {msg: error}});
    // return Promise.reject(error);
  }
);

const _http: Record<string, Function> = {
  post:(url: string,params: Record<string, any>,success: Function,err?: Function)=>{
    _axios.post(url,params).then((res: any)=>{
      if(!res){
        return;
      }
      if(res.code === 200){
        success(res);
      }else {
        message(res.msg);
        if (typeof err === 'function') {
          err(res);
        }
      }
    })
  },
  get: (url: string,params: Record<string, any>,success: Function,err?: Function)=> {
    _axios.get(url, {params: params}).then((res: any) => {
		if(!res){
			return;
		}
		if (res.code === 200) {
			success(res);
		} else {
			message(res.msg);
			if (typeof err === 'function') {
				err(res);
			}
		}
    })
  },
	delete: (url: string,params: Record<string, any>,success: Function,err?: Function)=>{
		_axios.delete(url,{params: params}).then((res: any) => {
			if(!res){
				return;
			}
			if (res.code === 200) {
				success(res);
			} else {
				message(res.msg);
				if (typeof err === 'function') {
					err(res);
				}
			}
		})
	},
  pay: (url: string,params: Record<string, any>,success: Function)=>{
    _axios.get(url,{params: params}).then((res: any) => {
      success(res)
    })
  }
}

export default _http;
