export default {
	// 把数组2的数据合并到数组1中，根据key判断
	mergeArrayObj: (array1: Array<any>,array2: Array<any>,key: string)=>{
		if(array1.length==0){
			array1 = array2;
		}else{
			array2.forEach(val=>{
				let flag = false;
				for(const item of array1){
					if(item[key] == val[key]){
						flag = true;
						break;
					}
				}
				if(!flag){
					array1.push(val);
				}
			})
		}
		return array1;
	},
	// 把数据添加到数组中，如果有则从数组中移除该数据，根据key判断
	addObj2Array: (array1: Array<any>,obj: any,key: string)=>{
		let flag = false;
		for(const i in array1){
			if(array1[i][key] == obj[key]){
				array1.splice(parseInt(i),1);
				flag = true;
				break;
			}
		}
		if(!flag){
			array1.push(obj);
		}
		return array1;
	},
	// 时间戳格式化为 年月日 时分秒 yyyy-MM-dd HH:dd:ss
	timestamp2DateTime(timestamp: number){
		if(timestamp){
			const date = new Date(timestamp);
			return `${date.getFullYear()}-${this.addPreZero((date.getMonth()+1).toString(),'0',2)}-${this.addPreZero(date.getDate().toString(),'0',2)} ${date.getHours()}:${this.addPreZero(date.getMinutes().toString(),'0',2)}:${this.addPreZero(date.getSeconds().toString(),'0',2)}`;
		}
		return '';
	},
	// 时间格式化为年月日 时分秒 yyyy-MM-dd HH:dd:ss
	dateFormart(date: Date){
		if(date){
			return `${date.getFullYear()}-${this.addPreZero((date.getMonth()+1).toString(),'0',2)}-${this.addPreZero(date.getDate().toString(),'0',2)} ${date.getHours()}:${this.addPreZero(date.getMinutes().toString(),'0',2)}:${this.addPreZero(date.getSeconds().toString(),'0',2)}`;
		}
		return '';
	},
	// 补位，数据，补位字符，补位后的长度
	addPreZero(data: string,char: string,num: number){
		for(let i=0; i<num; i++){
			data = char+data;
		}
		return data.slice(-num);
	},
	getUrlKey: function (key: string) {
		let value = null;
		const query = window.location.href.substring(window.location.href.indexOf('?')+1);
		const vars = query.split("&");
		for (let i=0;i<vars.length;i++) {
			const pair = vars[i].split("=");
			if(pair[0] == key){
				value = pair[1];
			}
		}
		return value;
	}
}